.prompt {
    min-height: 83px;
    border-radius: 15px;
    background: var(--chakra-colors-white);
    border: 1px solid #ccc;
    padding: 5px;
    font-size: 17px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.prompt:hover {
    background: #cccccc;
}
